





























import Vue from "vue";
import { createNamespacedHelpers } from "vuex";

const { mapGetters } = createNamespacedHelpers("task");
export default Vue.extend({
  data() {
    return {
      isShowShare: false,
    };
  },
  computed: {
    ...mapGetters(["score"]),
  },

  methods: {
    clickShare() {
      console.log("share");
      this.isShowShare = !this.isShowShare;
    },
  },
});
